import React, { useState, useRef, useEffect } from "react";
import PropTypes from "prop-types";
import Grid from "./Grid";
import { useDispatch } from "react-redux";
import { updateItem, removeItem } from "../redux/shopping-cart/cartSlice";

const CartItem = (props) => {
  const [item, setItem] = useState(props.item);

  const [quantity, setQuantity] = useState(props.item.quantity);

  const dispatch = useDispatch();

  useEffect(() => {
    setItem(props.item);
    setQuantity(props.item.quantity);
  }, [props.item]);

  const updateQty = (opt) => {
    if (opt === "+") {
      dispatch(updateItem({ ...item, quantity: quantity + 1 }));
    }
    if (opt === "-") {
      dispatch(
        updateItem({ ...item, quantity: quantity - 1 === 0 ? 1 : quantity - 1 })
      );
    }
  };

  const removeCartItem = () => {
    dispatch(removeItem(item));
  };

  return (
    <div className="cart-item">
      <div className="cart-item__image">
        <img src={item.product.img01} alt="" />
      </div>

      <div className="cart-item__title">
        <h5>{item.product.title}</h5>
      </div>

      <div className="cart-item__price">
        <h5>{item.price}</h5>
      </div>
      <div className="cart-item__qty">
        <div className="cart-item__qty__btn">
          <i className="bx bx-minus" onClick={() => updateQty("-")}></i>
        </div>
        <div className="cart-item__qty__input">
          <h5>{item.quantity}</h5>
        </div>
        <div className="cart-item__qty__btn">
          <i className="bx bx-plus" onClick={() => updateQty("+")}></i>
        </div>
      </div>
      <div className="cart-item__trash">
        <i className="bx bx-trash" onClick={() => removeCartItem()}></i>
      </div>
    </div>
  );
};

CartItem.propTypes = {
  item: PropTypes.object,
};

export default CartItem;
